@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
body {
  /* Padding and margin style*/
  margin: 0;
  padding: 0;

  /* Background style */
  background: linear-gradient(90deg, rgb(78, 252, 223) 0%, rgb(128, 255, 128) 100%);

  /* Font style */
  font-family: 'Varela Round';
}

button{
  /* Cursor */
  cursor: pointer;
}
.App {
  text-align: center;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;

}
nav {
    /* Margin and padding */
    padding: 15px;

    /* Display styles */
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    /* Overflow styles */
    overflow: hidden;

    /* Background */
    background-color: rgba(168, 168, 168, 0.226);
}
.logo{
    /* Margin and padding */
    margin-left: 20px;
    padding: 5px;

    /* Sizes */
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.logo img{
    width: 60px;
}
.imageLinkForm{
    /* Distribution */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /* Margin and padding */
    margin-top: 20px;
    padding: 0;
}

.imageLinkForm p{
    /* Font style */
    font-size: larger;
}

.LinkForm{
    /* Border */
    border-radius: 20px;

    /* Background */
    background-color: transparent;
    box-shadow: 0px 0px 0px 5px rgba(0, 0, 0, 0.185);

    /* Margin and Padding */
    margin-top: 20px;
    padding: 0px;

    /* Size */
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;

    /* Distribution */
    display: flex;
    justify-content: center;
    align-items: center;
}

.LinkForm input[type=text] {
    /* Border */
    border-radius: 20px 0px 0px 20px;
    border: 0;

    /* Size */
    height: 30px;
    width: 40vw;

    /* Font */
    font-size: large;
    font-weight: bold;

    /* Margin and Padding */
    padding: 10px;
}

.LinkForm input[type=text]:focus, .LinkForm button:focus{
    outline: none;
}

.LinkForm button {
    /* Border */
    border-radius: 0px 20px 20px 0px;
    border: 0;

    /* Size */
    height: 30px;
    width: 7vw;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    /* Font */
    font-size: large;
    font-weight: bold;

    /* Cursor */
    cursor: pointer;

    /* Background */
    background-color: #EFEFEF;

    /* Transitions */
    transition: all 0.5s;
}

.LinkForm button:hover, .LinkForm input[type=text]:hover{
    /* Background */
    background-color: black;

    /* Font style */
    color: white;
}
.rank h2{
    /* Font style */
    font-size: x-large;

    /* Margin and padding */
    margin: 0;
    padding: 0;
}

.rank h2:first-child{
    /* Font style */
    font-size: xx-large;
    
    /* Margin and padding */
    margin-top: 20px;
}
